.burger__wrap {
  position: absolute;
  border-radius: 4px;
  display: none;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
  background-color: var(--colorGreen);
  width: 40px;
  height: 40px;
  padding: 5px;
}

.burger {
  width: 22px;
  height: 15px;
  transition: all 0.3s ease 0s;
}
.burger:after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 22px;
  height: 2px;
  background-color: var(--colorMain);
  transition: all 0.3s ease 0s;
  transform: translate(-50%, -8px);
}
.burger:before {
  content: "";
  position: absolute;
  top: auto;
  bottom: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 22px;
  height: 2px;
  background-color: var(--colorMain);
  transition: all 0.3s ease 0s;
  transform: translate(-50%, 8px);
}
.burger span {
  position: absolute;
  width: 22px;
  height: 2px;
  display: block;
  left: 50%;
  display: inline-block;
  background-color: var(--colorMain);
  top: 50%;
  transform: translate(-50%, -50%);
  transition: all 0.3s ease 0s;
}
.burger.open:after {
  transform: rotate(-45deg) translateY(-1px);
  left: 11px;
}
.burger.open:before {
  transform: rotate(45deg) translateY(2px);
  left: 11px;
}
.burger.open span {
  transform: scale(0);
}

