// Trusted block

.trusted {
  padding-top: 100px;
  padding-bottom: 100px;
  background: linear-gradient(94.89deg, #fbfbfb 1.63%, rgba(251, 251, 251, 0.58) 94.66%);
  @media (max-width: 768px) {
    padding-top: 45px;
  }
  @media (max-width: 576px) {
    padding-bottom: 50px;
  }

  &__suptitle {
    text-align: center;
    font-size: 15px;
    line-height: 24px;
  }

  &__title {
    margin-top: 10px;
    text-align: center;
    font-size: 48px;
    letter-spacing: 0.5px;
    @media (max-width: 992px) {
      font-size: 32px;
      line-height: 37px;
      margin: 10px auto 0 auto;
    }
    @media (max-width: 576px) {
      max-width: 312px;
    }
  }

  &__wrap {
    margin-top: 48px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @media (max-width: 992px) {
      flex-wrap: wrap;
    }
    @media (max-width: 831px) {
      justify-content: center;
    }
    @media (max-width: 576px) {
      margin-top: 35px;
    }
  }

  &__item {
    display: flex;
    span {
      display: inline;
      width: 40px;
      height: 40px;
      margin-right: 15px;
      font-family: "Gotham";
      font-size: 26px;
      font-weight: 700;
      line-height: 30px;
      letter-spacing: 0.5px;
      background-color: var(--colorGreen);
      padding: 11px 13px;
      border-radius: 5px;
      @media (max-width: 576px) {
        margin: 0 auto;
      }
    }
    @media (max-width: 576px) {
      flex-direction: column;
    }
  }

  &__content {
    padding-top: 20px;
    max-width: 341px;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 140px 100px;
    @media (max-width: 1200px) {
      grid-template-rows: 170px 150px;
    }
    @media (max-width: 992px) {
      grid-template-rows: 120px 100px;
    }
    @media (max-width: 992px) {
      grid-template-rows: 130px 80px;
    }
  }

  &__text {
    line-height: 26px;
    color: #170038;
  }

  &__name {
    margin-top: 20px;
    font-family: "Gotham";
    font-size: 26px;
    line-height: 30px;
    letter-spacing: 0.5px;
    font-weight: 700;
  }
}

