    @media (max-width: 1433px) {
      text-align: center;
    }
  }

  &__wrap {
    margin-top: 30px;
    display: flex;
    justify-content: space-between;
    gap: 50px;
    flex-wrap: wrap;
    @media (max-width: 1433px) {
      justify-content: center;
    }
    @media (max-width: 1027px) {
      row-gap: 30px;
    }
    @media (max-width: 768px) {
      column-gap: 30px;
    }
  }
  &__wrapbtn {
    margin-top: 30px;
    margin-bottom: 30px;
    text-align: center;
    display: none;
    @media (max-width: 576px) {
      display: block;
    }
  }

  &__item {
    width: 296px;
    border-radius: 6px;
    display: block;
  }

  &__img {
    border-radius: 6px;
    height: 378px;
    overflow: hidden;
    img {
      transition: all 0.3s ease 0s;
      &:hover {
        transform: scale(1.05);
        filter: sepia(100%) blur(1px);
      }
    }
  }

  &__info {
    margin-top: 20px;
    transition: all 0.3s ease 0s;
    &:hover {
      .blog__date,
      .blog__name {
        color: #594477;
        text-shadow: 0px 0px 0.5px rgba(#463361, 0.5);
      }
    }
  }

  &__date {
    font-size: 15px;
    line-height: 24px;
    color: var(--colorMain);
    transition: all 0.3s ease 0s;
  }

  &__name {
    font-family: "Gotham";
    font-size: 23px;
    line-height: 25px;
    letter-spacing: 0.5px;
    color: var(--colorMain);
    margin-top: 10px;
    font-weight: 700;
    transition: all 0.3s ease 0s;
  }

  &__pagination {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 100px;
    .nav-links {
      display: flex;
      align-items: center;
      gap: 10px;
    }
    .page-numbers {
      font-size: 15px;
      line-height: 15px;
      color: var(--colorMain);
      width: 40px;
      height: 40px;
      border: 1px solid var(--colorMain);
      border-radius: 12px;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      transition: all 0.3s ease 0s;
      margin: 0 auto;
      cursor: pointer;
      &.dots {
        border: none;
        font-size: 28px;
        color: #bbbbbb;
        align-items: flex-start;
        padding-top: 5px;
        &:hover {
          background-color: transparent;
          border: none;
          color: #bbbbbb;
        }
      }
      &:hover {
        color: var(--colorWhite);
        background-color: var(--colorGreen);
        border: 1px solid var(--colorGreen);
      }
      &.current {
        color: var(--colorWhite);
        background-color: var(--colorGreen);
        border: 1px solid var(--colorGreen);
      }
    }
    @media (max-width: 992px) {
      margin-top: 50px;
    }
    @media (max-width: 576px) {
      display: none;
    }
  }
}

