// Forms autoristion, registration

.autorisation {
  background: linear-gradient(-50deg, #fff 50%, rgb(#14efb5, 0.2) 100%);
  // .autorisation__wrap
  .error {
    color: red;
  }

  &__wrap {
    width: 100%;
    max-width: 557px;
    margin: 0 auto;
    text-align: center;
  }

  // .autorisation__title

  &__title {
  }

  // .autorisation__subtitle

  &__subtitle {
    line-height: 26px;
    margin-top: 6px;
  }

  // .autorisation__form
  &__passwrap {
    position: relative;
    img {
      position: absolute;
      right: 15px;
      bottom: 20px;
      cursor: pointer;
    }
  }
  &__form {
    margin-top: 45px;
    label {
      display: block;
      text-align: left;
      line-height: 24px;
      margin-bottom: 15px;
    }
    input[type="text"],
    input[type="password"],
    input[type="email"] {
      width: 100%;
      border: none;
      border-bottom: 1px solid #000000;
      padding-top: 12px;
      padding-bottom: 12px;
      padding-left: 5px;
      outline: none;
      margin-bottom: 10px;
      background: transparent;
      font-size: 16px;
      border-radius: 0;
      &::placeholder {
        line-height: 24px;
        color: #cbcbcb;
      }
      &.error {
        border-color: red;
      }
      /* &:focus {
        background-color: rgba(#14efb5, 0.1);
      } */
    }

    input[type="password"] {
      position: relative;
    }
    svg {
      position: absolute;
      right: 0;
    }
  }

  // .autorisation__switch
  &__switch {
    margin-top: 32px !important;
  }
  // .autorisation__checkbox

  &__checkbox {
    display: flex;
    align-items: center;
    label {
      margin-bottom: 0;
      margin-left: 15px;
      @media (max-width: 576px) {
        font-size: 13px;
      }
    }
    a {
      margin-left: auto;
      font-size: 16px;
      line-height: 24px;
      text-decoration-line: underline;
      color: #170038;
      &:hover {
        text-decoration-line: none;
      }
      @media (max-width: 576px) {
        font-size: 13px;
      }
    }
    input[type="checkbox"] {
      position: relative;
      cursor: pointer;
    }
    input[type="checkbox"]:before {
      content: "";
      display: block;
      position: absolute;
      width: 22px;
      height: 22px;
      top: -4px;
      left: 0;
      border: 1px solid #170038;
      border-radius: 6px;
      background-color: white;
    }
    input[type="checkbox"]:checked:after {
      content: "";
      display: block;
      width: 8px;
      height: 14px;
      border: solid var(--colorGreen);
      border-width: 0 2px 2px 0;
      -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
      transform: rotate(45deg);
      position: absolute;
      top: -2px;
      left: 7px;
    }
    &_reg {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      max-width: 400px;
      input {
        margin-top: 10px;
      }
      label {
        margin-left: 20px;
        margin-top: 15px;
        text-align: left !important;
      }
      div {
        display: flex;
        align-items: center;
      }
    }
  }

  // .autorisation__btn

  &__btn {
    &:first-child {
      display: block;
      margin-left: auto;
      margin-bottom: 16px;
    }
  }

  .button {
    // .button__wrap

    &__wrap {
      margin-top: 30px;
      text-align: right;
      &_reg {
        display: flex;
        justify-content: space-between;
        text-align: left;
        a {
          display: flex;
          max-width: 300px;
          padding-left: 32px;
          line-height: 24px;
          text-decoration-line: underline;
          color: var(--colorMain);
          &:hover {
            text-decoration: none;
          }
          @media (max-width: 576px) {
            padding-left: 0;
            font-size: 13px;
            max-width: 60%;
          }
        }
      }
    }
  }
  select {
    -moz-appearance: none;
    -webkit-appearance: none;
    appearance: none;
    border: none;
    border-bottom: 1px solid #000000;
    width: 100%;
    outline: none;
    padding-top: 12px;
    padding-bottom: 12px;
    padding-left: 5px;
    line-height: 24px;
    color: #cbcbcb;
    margin-bottom: 10px;
    background-color: transparent;
    background-image: url("../img/bird.png");
    background-size: 14px 9px;
    background-position: 97% center;
    background-repeat: no-repeat;
    cursor: pointer;
  }
  .select__wrap {
    /* position: relative;
    &::after {
      content: "";
      position: absolute;
      top: 20px;
      right: 15px;
      width: 10px;
      height: 10px;
      background-color: transparent;
      border: 1px solid #000;
      border-top: none;
      border-left: none;
      transform: rotate(45deg);
      cursor: pointer;
    } */
  }
  option {
    color: var(--colorMain);
  }
  .reg_form,
  .reg_title_wrap {
    display: none;
  }
  .message {
    padding: 17px 40px 17px 55px;
    font-family: "Poppins";
    line-height: 26px;
    display: block;
    margin: 0 auto 15px auto;
    text-align: center;
    background: #cdfff2;
    border-radius: 6px;
    background-image: url("../img/bg-message.png");
    background-position: 32px center;
    background-repeat: no-repeat;
    background-size: 23px 23px;
    @media (max-width: 500px) {
      font-size: 14px;
      line-height: 18px;
      background-position: 20px center;
    }
    &__email {
      max-width: 700px;
      position: relative;
      //display: none;
      &.active {
        display: block;
      }
      .close {
        position: absolute;
        cursor: pointer;
        top: 12px;
        right: 25px;
        font-size: 25px;
        margin-top: 5px;
        @media (max-width: 652px) {
          top: 5px;
          right: 10px;
        }
      }
    }
    &__login {
      max-width: 445px;
      position: relative;
      //display: none;
      &.active {
        display: block;
      }
      .close {
        position: absolute;
        cursor: pointer;
        top: 12px;
        right: 25px;
        font-size: 25px;
        margin-top: 5px;
        @media (max-width: 652px) {
          top: 5px;
          right: 10px;
        }
      }
    }
  }
}

